





















.home_page-wrapper {
  display: flex;
  flex-direction: row;
}
.home_page_avatar-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    height: 100%;
    object-fit: cover;
  }

  @media (max-width: 1280px) {
    &::after {
      content: '';
      @apply w-full h-full bg-white opacity-40 absolute top-0 right-0 z-0;
    }
  }
}
